
'use client';

import type { ReactNode } from 'react';
import { createContext, useState, useEffect, useCallback } from 'react';
import {
  getAuth,
  onAuthStateChanged,
  signOut,
  signInWithPopup,
  GoogleAuthProvider,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  sendPasswordResetEmail,
  updateProfile,
  type User,
} from 'firebase/auth';
import { firebaseApp } from '@/lib/firebase';
import { doc, setDoc, getDoc, serverTimestamp, getFirestore, type Firestore, collection, getDocs, query, where, arrayUnion } from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL, getStorage, type FirebaseStorage } from 'firebase/storage';
import type { ProfileData, TestResult } from '@/types';


interface AuthContextType {
  user: User | null;
  profile: ProfileData | null;
  login: (email: string, pass: string) => Promise<ProfileData | null>;
  signup: (values: ProfileData & {password: string}) => Promise<void>;
  loginWithGoogle: () => Promise<ProfileData | null>;
  sendPasswordReset: (email: string) => Promise<void>;
  logout: () => void;
  isLoading: boolean;
  updateUserProfile: (data: Partial<ProfileData>, newPhotoFile: File | null) => Promise<void>;
  upgradeToPro: () => Promise<void>;
  getAllUsers: () => Promise<ProfileData[]>;
  getUserTestHistory: (userId: string) => Promise<TestResult[]>;
  getUserProfile: (userId: string) => Promise<ProfileData | null>;
  getCompletedGameLevels: () => Promise<number[]>;
  saveCompletedGameLevel: (levelId: number) => Promise<void>;
}

export const AuthContext = createContext<AuthContextType | undefined>(undefined);

const auth = getAuth(firebaseApp);
const googleProvider = new GoogleAuthProvider();

// Lazily initialize services to avoid loading issues
let db: Firestore;
let storage: FirebaseStorage;

function getDb() {
    if (!db) {
        db = getFirestore(firebaseApp);
    }
    return db;
}

function getAppStorage() {
    if (!storage) {
        storage = getStorage(firebaseApp);
    }
    return storage;
}


export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null);
  const [profile, setProfile] = useState<ProfileData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  
  const fetchProfile = useCallback(async (user: User): Promise<ProfileData | null> => {
    const db = getDb();
    const userDocRef = doc(db, 'users', user.uid);
    const userDoc = await getDoc(userDocRef);
    if (userDoc.exists()) {
      const profileData = userDoc.data() as ProfileData;
      setProfile(profileData);
      return profileData;
    }
    return null;
  }, []);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      setIsLoading(true);
      setUser(user);
      if (user) {
        await fetchProfile(user);
      } else {
        setProfile(null);
      }
      setIsLoading(false);
    });
    return () => unsubscribe();
  }, [fetchProfile]);

  const login = useCallback(async (email: string, pass: string): Promise<ProfileData | null> => {
    const userCredential = await signInWithEmailAndPassword(auth, email, pass);
    return await fetchProfile(userCredential.user);
  }, [fetchProfile]);
  
  const signup = useCallback(async (values: ProfileData & {password: string, confirmPassword?: string}) => {
      const userCredential = await createUserWithEmailAndPassword(auth, values.email, values.password);
      const user = userCredential.user;
      
      const storage = getAppStorage();
      const db = getDb();

      let photoURL = '';
      if (values.profilePhoto && (values.profilePhoto as FileList).length > 0) {
        const file = (values.profilePhoto as FileList)[0];
        const storageRef = ref(storage, `profile_photos/${user.uid}`);
        await uploadBytes(storageRef, file);
        photoURL = await getDownloadURL(storageRef);
      }

      await updateProfile(user, {
          displayName: `${values.firstName} ${values.surname}`,
          photoURL: photoURL
      });

      const userDocRef = doc(db, 'users', user.uid);
      const { password, confirmPassword, ...profileData } = values;
      await setDoc(userDocRef, {
        ...profileData,
        uid: user.uid,
        email: user.email,
        profilePhoto: photoURL || user.photoURL,
        createdAt: serverTimestamp(),
        subscriptionStatus: 'free',
        isAdmin: false, // Default to not admin
      });

      setUser(auth.currentUser); // refresh user state
      await fetchProfile(user);

  }, [fetchProfile]);

  const loginWithGoogle = useCallback(async (): Promise<ProfileData | null> => {
    const result = await signInWithPopup(auth, googleProvider);
    const user = result.user;
    const db = getDb();
    const userDocRef = doc(db, 'users', user.uid);
    const userDoc = await getDoc(userDocRef);

    if (!userDoc.exists()) {
      const [firstName, ...rest] = user.displayName?.split(' ') || [];
      const surname = rest.pop() || '';
      const middleName = rest.join(' ');
      
       await setDoc(userDocRef, {
        uid: user.uid,
        email: user.email,
        firstName: firstName || '',
        surname: surname,
        middleName: middleName,
        profilePhoto: user.photoURL,
        createdAt: serverTimestamp(),
        subscriptionStatus: 'free',
        isAdmin: false,
      });
    }
    return await fetchProfile(user);
  }, [fetchProfile]);
  
  const sendPasswordReset = useCallback(async (email: string) => {
    await sendPasswordResetEmail(auth, email);
  }, []);

  const updateUserProfile = useCallback(async (data: Partial<ProfileData>, newPhotoFile: File | null) => {
    if (!user) throw new Error("User not logged in");
  
    const storage = getAppStorage();
    const db = getDb();
    const profileDataToSave: Partial<ProfileData> = { ...data };
    let photoURL: string | undefined = profile?.profilePhoto as string;
  
    if (newPhotoFile) {
      const storageRef = ref(storage, `profile_photos/${user.uid}`);
      await uploadBytes(storageRef, newPhotoFile);
      photoURL = await getDownloadURL(storageRef);
      profileDataToSave.profilePhoto = photoURL;
    }
    
    if (data.firstName && data.surname) {
        await updateProfile(user, {
            displayName: `${data.firstName} ${data.surname}`,
            photoURL: photoURL || user.photoURL
        });
    }
  
    const userDocRef = doc(db, 'users', user.uid);
    await setDoc(userDocRef, profileDataToSave, { merge: true });
  
    setUser(auth.currentUser);
    await fetchProfile(user);
  
  }, [user, profile, fetchProfile]);

  const upgradeToPro = useCallback(async () => {
    if (!user) throw new Error("User not logged in");
    const db = getDb();
    const userDocRef = doc(db, 'users', user.uid);
    await setDoc(userDocRef, { subscriptionStatus: 'pro' }, { merge: true });
    await fetchProfile(user);
  }, [user, fetchProfile]);

  const logout = useCallback(async () => {
    await signOut(auth);
  }, []);
  
  const getAllUsers = useCallback(async (): Promise<ProfileData[]> => {
    if (profile?.isAdmin) {
      const db = getDb();
      const usersCol = collection(db, 'users');
      const userSnapshot = await getDocs(usersCol);
      const userList = userSnapshot.docs.map(doc => doc.data() as ProfileData);
      return userList;
    }
    return [];
  }, [profile]);

  const getUserTestHistory = useCallback(async (userId: string): Promise<TestResult[]> => {
     if (profile?.isAdmin) {
        const db = getDb();
        const q = collection(db, 'testResults');
        const querySnapshot = await getDocs(q); // Simplified for admin
        const history: TestResult[] = [];
        querySnapshot.forEach((doc) => {
            const data = doc.data();
            if (data.userId === userId) {
                 history.push({
                    id: doc.id,
                    ...data,
                    createdAt: data.createdAt.toDate(),
                 } as TestResult);
            }
        });
        return history.sort((a,b) => b.createdAt.getTime() - a.createdAt.getTime());
     }
     return [];
  }, [profile]);

  const getUserProfile = useCallback(async (userId: string): Promise<ProfileData | null> => {
     if (profile?.isAdmin) {
        const db = getDb();
        const userDocRef = doc(db, 'users', userId);
        const userDoc = await getDoc(userDocRef);
        return userDoc.exists() ? userDoc.data() as ProfileData : null;
     }
     return null;
  }, [profile]);
  
  const getCompletedGameLevels = useCallback(async (): Promise<number[]> => {
    if (!user) return [];
    const db = getDb();
    const docRef = doc(db, "gameProgress", user.uid);
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      return docSnap.data().completedLevels || [];
    }
    return [];
  }, [user]);

  const saveCompletedGameLevel = useCallback(async (levelId: number) => {
    if (!user) return;
    const db = getDb();
    const docRef = doc(db, "gameProgress", user.uid);
    await setDoc(docRef, {
      completedLevels: arrayUnion(levelId)
    }, { merge: true });
  }, [user]);


  const value = { user, profile, login, signup, loginWithGoogle, logout, isLoading, updateUserProfile, upgradeToPro, sendPasswordReset, getAllUsers, getUserTestHistory, getUserProfile, getCompletedGameLevels, saveCompletedGameLevel };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
}
